import {
  Component,
  OnInit,
  AfterViewInit,
  HostListener,
  ChangeDetectorRef
} from '@angular/core';
import { GRAPH_OPTIONS } from '@shared/constants/graph-view';
import { PortfolioService } from '@modules/dashboard/api/portfolio/portfolio.service';
import { Title } from '@angular/platform-browser';
import { SessionStorageService } from '@shared/services/session-storage/session-storage.service';
import { CustomChartService } from '@shared/services/custom-chart/custom-chart.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { DragAndDropService } from '@shared/services/drag-and-drop/drag-and-drop.service';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.scss']
})
export class PortfolioComponent implements OnInit, AfterViewInit {
  graphValues;
  graphFilters;
  selectedGraphFilter;
  assetPerformanceFilters;
  selectedAssetPerformanceFilter;
  haveGraphValues = false;
  haveAssetPerformanceData = false;
  isDesktop;
  isTablet;
  isMobile;
  assetPerformanceData;
  isFilterAssetPerfOpen = false;
  filterAssetPerfDropHeight;
  assetPerformanceFilterHeads;
  firstName = '';
  userName;
  currentValue;
  totalReturn;
  svgIconsDesktop = [
    {
      id: '#icon-filter',
      originals: {
        fill: '#626262',
        width: '35px',
        height: '35px'
      },
      modifiers: {
        fill: '#626262',
        width: '22px',
        height: '22px'
      }
    },
    {
      id: '#icon-income-blue',
      originals: {
        fill: '#788195',
        width: '28px',
        height: '36px'
      },
      modifiers: {
        fill: '#6580B6',
        width: '25px',
        height: '33px'
      }
    },
    {
      id: '#icon-capital-blue',
      originals: {
        fill: '#788195',
        width: '32px',
        height: '32px'
      },
      modifiers: {
        fill: '#6580B6',
        width: '28px',
        height: '28px'
      }
    }
  ];

  svgIconsMobile = [
    {
      id: '#icon-asset-gray',
      originals: {
        fill: '#2C2C2C'
      },
      modifiers: {
        fill: '#D1D1D1'
      }
    },
    {
      id: '#icon-income-v2-gray',
      originals: {
        fill: '#2C2C2C'
      },
      modifiers: {
        fill: '#D1D1D1'
      }
    },
    {
      id: '#icon-growth-v2-gray',
      originals: {
        fill: '#2C2C2C'
      },
      modifiers: {
        fill: '#D1D1D1'
      }
    },
    {
      id: '#icon-returns-gray',
      originals: {
        fill: ['#2C2C2C', '#000000', '#2C2C2C']
      },
      modifiers: {
        fill: ['#D1D1D1', '#D1D1D1', '#D1D1D1']
      }
    }
  ];
  selectedDatePurchaseFilter = '';
  porfolioGraphHeight;
  haveParts;

  modalRef: BsModalRef;
  modalConfig = {
    ignoreBackdropClick: true,
    class: 'modal-sm'
  };

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
      this.updatePorfolioValueHeight();
    }, 250);
  }

  constructor(
    private _portfolioService: PortfolioService,
    private title: Title,
    private _sessionStorageService: SessionStorageService,
    private _customChartService: CustomChartService,
    private _viewportService: ViewportService,
    private _dragAndDropService: DragAndDropService,
    private changeDetectorRef: ChangeDetectorRef
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.updateIcons(this.svgIconsDesktop);
      this.isDesktop = this._viewportService.isDesktop();
      this.isTablet = this._viewportService.isTablet();
      this.isMobile = this._viewportService.isMobile();
    });
  }

  ngOnInit() {
    this.title.setTitle(`Partbnb - My Portfolio`);
    this.graphFilters = GRAPH_OPTIONS.map(filter => filter.name);
    this.assetPerformanceFilters = [];
    this.selectedGraphFilter = GRAPH_OPTIONS[0];
    this.userName = this._sessionStorageService.getSession('username');
    this.selectedAssetPerformanceFilter = {
      countryId: null,
      datePurchased: '',
      pageNumber: 1,
      pageSize: 10
    };
    this.currentValue = 0.0;
    this.totalReturn = {
      netWorth: 0.0,
      percentage: 0.0
    };
    this.haveParts = false;
    this.getAssetPerformance();
  }

  ngAfterViewInit() {
    this._viewportService.checkViewSizes();
    this.updatePorfolioValueHeight();
    this.getGraphValues();
  }

  setSelectedGraphFilter(evt) {
    const selected = GRAPH_OPTIONS.filter(filter => filter.name === evt);
    this.selectedGraphFilter = selected[0];
    this.getGraphValues();
  }

  updateIcons(svgValues) {
    for (const svg of svgValues) {
      const svgRef = document.querySelector(svg.id);
      let svgRefString = svgRef.innerHTML || '';
      for (const svgStyle of Object.keys(svg.originals)) {
        if (typeof svg.originals[svgStyle] === 'object') {
          svg.originals[svgStyle].map((value, index) => {
            svgRefString = svgRefString.replace(
              `${svgStyle}="${value}"`,
              `${svgStyle}="${svg.modifiers[svgStyle][index]}"`
            );
          });
        } else {
          svgRefString = svgRefString.replace(
            `${svgStyle}="${svg.originals[svgStyle]}"`,
            `${svgStyle}="${svg.modifiers[svgStyle]}"`
          );
        }
      }
      svgRef.innerHTML = svgRefString;
    }
  }

  getGraphValues() {
    this.graphValues = [];
    this._portfolioService
      .getPortfolioValue(this.userName, {
        timeFrame: this.selectedGraphFilter.key
      })
      .subscribe(data => {
        this.graphValues = data.portfolioData;
        this.currentValue =
          data.currentValue.toLocaleString(undefined, {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2
          }) || this.currentValue;
        this.totalReturn = {
          netIncome: data.totalReturn.netIncome.toLocaleString(undefined, {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2
          }),
          percentage: data.totalReturn.percentage.toLocaleString(undefined, {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2
          })
        };
        this.haveParts = data.hasParts;
        this._dragAndDropService.setAddFundsBannerStatus(!this.currentValue);
        this.haveGraphValues =
          (this.graphValues.length === 0 && !!this.graphValues[0].valueAdded) ||
          this.graphValues.length > 0;
        this._customChartService.updateChartData(this.graphValues);
      });
  }

  getAssetPerformance() {
    this.assetPerformanceData = [];
    this._portfolioService
      .getAssetPerformance(this.userName, this.selectedAssetPerformanceFilter)
      .subscribe(data => {
        this.assetPerformanceFilters =
          data.hasOwnProperty('options') && data.options;
        this.haveAssetPerformanceData =
          data.hasOwnProperty('totalPerformance') &&
          data.hasOwnProperty('assetPerformance');
        this.assetPerformanceFilterHeads = Object.keys(
          this.assetPerformanceFilters
        );
        let heightCounter = 0;
        this.assetPerformanceData = data;
        this.assetPerformanceFilterHeads.map(heads => {
          heightCounter += 33;
          heightCounter += this.assetPerformanceFilters[heads].length * 35;
        });
        this.filterAssetPerfDropHeight = `${heightCounter}px`;
        if (this.isMobile && !this.haveAssetPerformanceData) {
          this.updateIcons(this.svgIconsMobile);
        }
      });
  }

  selectingAssetPerfFilter() {
    this.isFilterAssetPerfOpen = !this.isFilterAssetPerfOpen;
  }

  selecteAssetPerfdFilter(option) {
    if (option.hasOwnProperty('countryId')) {
      if (this.checkOptionSelected(option)) {
        this.selectedAssetPerformanceFilter.countryId = null;
      } else {
        this.selectedAssetPerformanceFilter.countryId = option.countryId;
      }
    } else if (option.hasOwnProperty('datePurchasedKey')) {
      if (this.checkOptionSelected(option)) {
        this.selectedAssetPerformanceFilter.datePurchased = '';
        this.selectedDatePurchaseFilter = '';
      } else {
        this.selectedAssetPerformanceFilter.datePurchased =
          option.datePurchasedKey;
        this.selectedDatePurchaseFilter = option.datePurchasedName;
      }
    }
    this.getAssetPerformance();
    this.selectingAssetPerfFilter();
  }

  getOptionName(option) {
    if (option.hasOwnProperty('countryName')) {
      return option.countryName;
    } else if (option.hasOwnProperty('datePurchasedName')) {
      return option.datePurchasedName;
    }
  }

  checkOptionSelected(option) {
    if (option.hasOwnProperty('countryId')) {
      return option.countryId === this.selectedAssetPerformanceFilter.countryId;
    } else if (option.hasOwnProperty('datePurchasedKey')) {
      return (
        option.datePurchasedKey ===
        this.selectedAssetPerformanceFilter.datePurchased
      );
    }
    return false;
  }

  updatePorfolioValueHeight() {
    if (!this.haveGraphValues) {
      const imageHeight = document.querySelector('.container');
      const partialCal = (imageHeight.clientWidth * 43) / 100;
      this.porfolioGraphHeight = `${
        this.isMobile ? partialCal + 150 : partialCal
      }px`;
      this.changeDetectorRef.detectChanges();
    }
  }
}
