import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { DashboardPropertiesService } from '@modules/dashboard/api';
import { environment } from '@env/environment';
import { BsModalService } from 'ngx-bootstrap';
import {
  ConfirmationComponent,
  ConfirmationProps
} from '@shared/components/modals/confirmation';
import { Title } from '@angular/platform-browser';
import { PlaidLinkHandler } from 'ngx-plaid-link/lib/ngx-plaid-link-handler';
import { PlaidConfig } from '@shared/services/plaid/interfaces';
import { PLAID } from '@env/environment';
import { NgxPlaidLinkService } from 'ngx-plaid-link';
import { PaymentMethodComponent } from '@shared/components/plaid';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import {
  CurrentUserService,
  WalletSharedService,
  GeneralService,
  ValidationSharedService
} from '@shared/services';
import { Router } from '@angular/router';
import { InvestorVerificationComponent } from '@shared/components/modals/investor-verification/investor-verification.component';
import { OrderPropertyModalComponent } from '@shared/components/modals/order-property-modal/order-property-modal.component';
import { PropertyService } from '@shared/api';
import { ModalService } from '@shared/services/modal/modal.service';

@Component({
  selector: 'app-my-properties',
  templateUrl: './my-properties.component.html',
  styleUrls: ['./my-properties.component.scss']
})
export class MyPropertiesComponent implements OnInit, AfterViewInit, OnDestroy {
  isPayment: boolean = false;
  username;
  myProperties;
  selectedProperty;
  isPropertyBlank: boolean = true;

  public baseUrl = environment.baseUrl;

  display = 'holding';
  displayOptions = [
    {
      name: 'Display Holding Details',
      value: 'holding'
    },
    {
      name: 'Display Yield Details',
      value: 'yield'
    }
  ];
  property = 0;
  sampleOptions = [
    {
      name: 'All Properties',
      value: 0
    }
  ];
  sorts = 'PurchaseDateDesc';
  sortsOptions = [
    {
      name: 'Latest Purchase',
      value: 'PurchaseDateDesc'
    },
    {
      name: 'Oldest Purchase',
      value: 'PurchaseDateAsc'
    },
    {
      name: 'Most Parts',
      value: 'PartsDesc'
    },
    {
      name: 'Least Parts',
      value: 'PartsAsc'
    },
    {
      name: 'Highest Initial Value',
      value: 'InitialValueDesc'
    },
    {
      name: 'Lowest Initial Value',
      value: 'InitialValueAsc'
    },
    {
      name: 'Highest Current Value',
      value: 'CurrentValueDesc'
    },
    {
      name: 'Lowest Current Value',
      value: 'CurrentValueAsc'
    }
  ];

  // Plaid
  public openingPlaid: boolean = false;
  private plaidLinkHandler: PlaidLinkHandler;

  private config: PlaidConfig = {
    clientName: 'Partbnb',
    apiVersion: 'v2',
    env: PLAID.ENV,
    institution: null,
    token: null,
    webhook: `${environment.apiUrl}/${PLAID.WEBHOOK}`,
    product: PLAID.PRODUCTS,
    key: PLAID.PUBLIC_KEY
  };

  constructor(
    private router: Router,
    private title: Title,
    private bsModalService: BsModalService,
    private plaidLinkService: NgxPlaidLinkService,
    private _currentUser: CurrentUserService,
    private _dashboardProperties: DashboardPropertiesService,
    private _proertyService: PropertyService,
    private _walletShared: WalletSharedService,
    private _general: GeneralService,
    private _modalService: ModalService,
    private _validationShared: ValidationSharedService
  ) {}

  ngOnInit() {
    this.title.setTitle(`Partbnb - My Properties`);
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.username = currentUser.username;
    this.getProperties();
    this._walletShared.updatePlaid.subscribe(plaid => {
      this.isPayment = plaid === 'payment';
    });
    this._walletShared.togglePlaid('payment');
  }

  ngAfterViewInit() {
    this.plaidLinkService
      .createPlaid(
        Object.assign({}, this.config, {
          onSuccess: (token, metadata) => this.onSuccess(token, metadata),
          onExit: () => {
            this.openingPlaid = false;
          },
          onEvent: () => {}
        })
      )
      .then((handler: PlaidLinkHandler) => {
        this.plaidLinkHandler = handler;
        this.isPayment = true;
      });
  }

  ngOnDestroy() {
    this.isPayment = false;
  }

  getProperties() {
    const postData = { PropertyId: this.property, SortingMode: this.sorts };
    this._dashboardProperties.getProperties(this.username, postData).subscribe(
      res => {
        this.isPropertyBlank = false;
        this._general.setPropertiesStatus(false);
        if (!res || !res.length) {
          this.myProperties = [];
          this.isPropertyBlank = true;
          this._general.setPropertiesStatus(true);
          return;
        }
        this.myProperties = res.map((obj, idx) => {
          if (idx === 0) {
            obj.toggle = true;
          } else {
            obj.toggle = false;
          }
          obj.display = 'holding';
          return obj;
        });
      },
      () => {
        this.myProperties = [];
        this.isPropertyBlank = true;
      }
    );
  }

  isPropertyInActive(status) {
    return status === 'Inactive';
  }

  isPropertyPending(item) {
    return (item && item.preOrderPaymentStatusID === 1) || false;
  }

  plaidOpen() {
    this.plaidLinkHandler.open();
  }

  plaidExit() {
    this.plaidLinkHandler.exit();
  }

  onSuccess(token, metadata) {
    if (!this.isPayment) {
      return;
    }
    this.openingPlaid = false;
    const postData = {
      public_token: token,
      data: metadata
    };
    this.openPayment(true, postData);
  }

  buyParts(item) {
    const { isAccreditedInvestor } = this._currentUser.getUserInfo();
    if (!isAccreditedInvestor) {
      this._validationShared.openWarningForNonAccredited();
      return;
    }
    this._proertyService.getPropertyDetails(item.propertyID).subscribe(res => {
      const details = {
        ...res.data
      };
      this._modalService.modalInstance(OrderPropertyModalComponent, {
        initialState: { details },
        animated: false,
        class: 'centered-modal'
      });
    });
  }

  openPayment(isReady: boolean = false, plaidData: any = null) {
    if (this.openingPlaid) {
      return;
    }
    this.openingPlaid = true;
    const initialState = {
      isReady,
      plaidData,
      username: this.username,
      property: this.selectedProperty
    };
    const modalRef = this._modalService.modalInstance(PaymentMethodComponent, {
      initialState,
      animated: false,
      keyboard: false,
      ignoreBackdropClick: true,
      class: 'plaid-modal'
    });

    (<PaymentMethodComponent>modalRef).onClose.subscribe(result => {
      if (result === 'add') {
        this.plaidOpen();
      } else if (result === 'done') {
        this.getProperties();
        this.openingPlaid = false;
      } else {
        this.openingPlaid = false;
      }
    });
  }

  makePayment(property, pending: boolean = false) {
    if (pending) {
      return;
    }
    const info = this._currentUser.getUserInfo();
    if (!info.isAccreditedInvestor) {
      this._validationShared.openWarningForNonAccredited();
      return;
    }
    this.selectedProperty = property;
    if (info && info.verificationStatus === VERIFICATION_STATUS.APPROVED) {
      this.openPayment();
    } else {
      const props: ConfirmationProps = {
        status: 'warning',
        title: 'You need to be verified as an Accredited Investor',
        desc: `We need to verify your status as an Accredited Investor before you can proceed.`,
        isButtonDisabled:
          info.verificationStatus === VERIFICATION_STATUS.PENDING || false,
        buttonLabel:
          info.verificationStatus === VERIFICATION_STATUS.PENDING
            ? 'Verification Pending'
            : 'Start Verification'
      };
      const modalRef = this._modalService.modalInstance(ConfirmationComponent, {
        initialState: { props },
        animated: false,
        class: 'confirmation'
      });

      (<ConfirmationComponent>modalRef).onClose.subscribe(result => {
        if (result) {
          // this.router.navigate(['/dashboard']);
          this.openInvestorVerify();
        }
      });
    }
  }

  openInvestorVerify() {
    this._modalService.modalInstance(InvestorVerificationComponent, {
      ignoreBackdropClick: true,
      class: 'modal-lg'
    });
  }

  toggleProperty(property) {
    this.myProperties.map(obj => {
      if (obj.propertyID !== property.propertyID) {
        obj.toggle = false;
      }
      return obj;
    });
    setTimeout(() => {
      property.toggle = !property.toggle;
    }, 500);
  }

  bookNow(url) {
    if (!!url) {
      window.open(url, '_blank');
    }
  }
}
