import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { WalletService } from '@shared/api';
import { BsModalService } from 'ngx-bootstrap';
import { AddFundsComponent } from '@shared/components/modals/add-funds/add-funds.component';
import {
  WalletSharedService,
  CurrentUserService,
  ValidationSharedService
} from '@shared/services';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import {
  ConfirmationProps,
  ConfirmationComponent
} from '@shared/components/modals/confirmation';
import { InvestorVerificationComponent } from '@shared/components/modals/investor-verification/investor-verification.component';
import { ModalService } from '@shared/services/modal/modal.service';
import { WithdrawFundsComponent } from '@shared/components/modals/withdraw-funds/withdraw-funds.component';

@Component({
  selector: 'app-wallet',
  templateUrl: './wallet.component.html',
  styleUrls: ['./wallet.component.scss']
})
export class WalletComponent implements OnInit {
  username;
  walletBalance: number = 0;
  recentTransactions: Array<any> = [];
  recentTotalPages: number = 0;
  pendingTransactions: Array<any> = [];
  isPendingTransactionEmpty: boolean = false;
  pendingTotalPages: number = 0;

  constructor(
    private title: Title,
    private bsModalService: BsModalService,
    private _wallet: WalletService,
    private _walletShared: WalletSharedService,
    private _currentUser: CurrentUserService,
    private _modalService: ModalService,
    private _validation: ValidationSharedService
  ) {}

  ngOnInit() {
    this.title.setTitle(`Partbnb - My Wallet`);
    const { username } = JSON.parse(localStorage.getItem('currentUser'));
    this.username = username;
    this.getInfo();

    this._walletShared.update.subscribe(addFund => {
      if (!addFund) {
        this.getInfo();
      }
    });
  }

  getWalletBalance() {
    if (this.username) {
      this._wallet.getUserWalletBalance(this.username).subscribe(res => {
        this.walletBalance = res.balance;
      });
    }
  }

  getTransactions() {
    this._wallet.getTransactions(this.username, 2).subscribe(res => {
      if (res && res.data) {
        this.recentTransactions = res.data;
        this.recentTotalPages = (res.meta && res.meta.total_pages) || 0;
      }
    });
  }

  getPendingTransactions() {
    this._wallet.getTransactions(this.username, 1).subscribe(
      res => {
        if (res && res.data) {
          this.pendingTransactions = res.data;
          this.isPendingTransactionEmpty =
            this.pendingTransactions && !this.pendingTransactions.length;
          this.pendingTotalPages = (res.meta && res.meta.total_pages) || 0;
        } else {
          this.isPendingTransactionEmpty = true;
        }
      },
      () => {
        this.isPendingTransactionEmpty = true;
      }
    );
  }

  getInfo() {
    this.getWalletBalance();
    this.getTransactions();
    this.getPendingTransactions();
  }

  changeRecentPage(value: number) {
    this._wallet.getTransactions(this.username, 2, 8, value).subscribe(res => {
      if (res && res.data) {
        this.recentTransactions = res.data;
      }
    });
  }

  changePendingPage(value: number) {
    this._wallet.getTransactions(this.username, 1, 8, value).subscribe(res => {
      if (res && res.data) {
        this.pendingTransactions = res.data;
      }
    });
  }

  addFund() {
    const status = this._validation.addFundChecker();
    if (status) {
      const modalRef = this._modalService.modalInstance(AddFundsComponent, {
        initialState: {},
        animated: false,
        class: 'plaid-modal'
      });
      (<AddFundsComponent>modalRef).onClose.subscribe(result => {
        if (result === 'done') {
          this.getInfo();
        }
      });
    }
  }

  // WIthdraw

  withdrawFund() {
    const modalRef = this.bsModalService.show(WithdrawFundsComponent, {
      initialState: {
        currentBalance: this.walletBalance
      },
      animated: false,
      class: 'plaid-modal'
    });
    (<WithdrawFundsComponent>modalRef.content).onClose.subscribe(result => {
      if (result === 'done') {
        this.getWalletBalance();
        this.getTransactions();
        this.getPendingTransactions();
      }
    });
  }
}
