import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ExecutiveLayout } from '@pages/layouts';
import { PortfolioComponent } from './pages/portfolio/portfolio.component';
import { WalletComponent } from './pages/wallet/wallet.component';
import { PreorderDashboardComponent } from './pages/preorder-dashboard/preorder-dashboard.component';
import { MyPropertiesComponent } from './pages/my-properties/my-properties.component';
import { MyProfileComponent } from '@shared/components/my-profile/my-profile.component';
import { BookToStayComponent } from './pages/book-to-stay/book-to-stay.component';
import { from } from 'rxjs';

export const routes: Routes = [
  {
    path: '',
    component: ExecutiveLayout,
    data: { module: 'dashboard' },
    children: [
      // { path: '', redirectTo: 'portfolio', pathMatch: 'full' }, // commented for the promo period
      { path: '', redirectTo: 'properties', pathMatch: 'full' },
      // {
      //   path: 'portfolio',
      //   component: PortfolioComponent,
      //   data: { pageTitle: 'Partbnb - My Portfolio', pageName: 'My Portfolio' }
      // }, // uncommented for development of this particular page
      {
        path: 'properties',
        component: MyPropertiesComponent,
        data: {
          pageTitle: 'Partbnb - My Properties',
          pageName: 'My Properties'
        }
      },
      // {
      //   path: 'wallet',
      //   component: WalletComponent,
      //   data: {
      //     pageTitle: 'Partbnb - My Wallet',
      //     pageName: 'My Wallet'
      //   }
      // },
      {
        path: 'my-profile',
        component: MyProfileComponent,
        data: {
          pageTitle: 'Partbnb - My Profile',
          pageName: 'My Profile'
        }
      },
      // {
      //   path: 'book-to-stay',
      //   component: BookToStayComponent,
      //   data: {
      //     pageTitle: 'Partbnb - Book To Stay',
      //     pageName: 'Book To Stay'
      //   }
      // }
      // {
      //   path: 'details',
      //   component: PreorderDashboardComponent,
      //   data: { pageTitle: 'Partbnb - My Dashboard' }
      // },
      // { path: '**', redirectTo: 'details' }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule {}
