import { Injectable } from '@angular/core';
import { environment } from '@env/environment';

import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { format } from '@shared/utilities/formatEmail';

import { PortfolioGraph, PortfolioAssetPerformance } from './portfolio.model';
import {
  portfolioGraphBuilderUI,
  portfolioAssetPerformanceUI
} from './portfolio.mapper';

@Injectable({
  providedIn: 'root'
})
export class PortfolioService {
  baseUrl = environment.apiUrl;

  constructor(private _http: HttpClient) {}

  getPortfolioValue(userName, params): Observable<PortfolioGraph> {
    return this._http
      .post(`${this.baseUrl}/users/${format(userName)}/portfolio-value`, params)
      .pipe(map(portfolioGraphBuilderUI));
  }

  getAssetPerformance(userName, params): Observable<PortfolioAssetPerformance> {
    return this._http
      .post(
        `${this.baseUrl}/users/${format(userName)}/asset-performance`,
        params
      )
      .pipe(map(portfolioAssetPerformanceUI));
  }
}
