import { Component, OnInit } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { AddFundsComponent } from '@shared/components/modals/add-funds/add-funds.component';
import { CurrentUserService } from '@shared/services/current-user/current-user.service';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import { DragAndDropService } from '@shared/services/drag-and-drop/drag-and-drop.service';
import {
  ConfirmationProps,
  ConfirmationComponent
} from '@shared/components/modals/confirmation';
import { ModalService } from '@shared/services/modal/modal.service';
import { ValidationSharedService } from '@shared/services';
@Component({
  selector: 'add-funds-banner',
  templateUrl: './add-funds-banner.component.html',
  styleUrls: ['./add-funds-banner.component.scss']
})
export class AddFundsBannerComponent implements OnInit {
  modalRef: BsModalRef;
  VERIFICATION_STATUS = VERIFICATION_STATUS;
  modalConfig = {
    ignoreBackdropClick: true,
    class: 'modal-lg plaid-modal'
  };
  firstName;
  verificationStatus;
  showBanner;
  isNoCurrentValue;

  constructor(
    private _dragAndDropService: DragAndDropService,
    private bsModalService: BsModalService,
    private _currentUserService: CurrentUserService,
    private _modalService: ModalService,
    private _validationShared: ValidationSharedService
  ) {
    this.isNoCurrentValue = false;
    this._dragAndDropService.showingAddFundsBanner.subscribe(status => {
      this.isNoCurrentValue = status;
    });
  }

  ngOnInit() {
    this._currentUserService.loanUserInfo().then(() => {
      this.firstName = this._currentUserService.getUserInfo().firstName;
      this.verificationStatus = this._currentUserService.getUserInfo().verificationStatus;
    });
  }

  showAddFundsModal(e) {
    e.preventDefault();
    const { isAccreditedInvestor } = this._currentUserService.getUserInfo();
    if (!isAccreditedInvestor) {
      this._validationShared.openWarningForNonAccredited();
      return;
    }

    this.modalRef = this._modalService.modalInstance(
      AddFundsComponent,
      this.modalConfig
    );
  }
}
