import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { format } from '@shared/utilities/formatEmail';
import { Property } from './dashboard-properies.model';
import { propertiesDashboardBuilderUI } from './dashboard-properties.mapper';

import { environment } from '@env/environment';
const baseUrl = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class DashboardPropertiesService {
  constructor(private _http: HttpClient) {}

  getProperties(userName, data): Observable<Array<Property>> {
    return this._http
      .post(`${baseUrl}/users/${format(userName)}/properties-table`, data)
      .pipe(map(propertiesDashboardBuilderUI));
  }
}
