import { Component, OnInit } from '@angular/core';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import { DragAndDropService } from '@shared/services/drag-and-drop/drag-and-drop.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { InvestorVerificationComponent } from '@shared/components/modals/investor-verification/investor-verification.component';
import { CurrentUserService } from '@shared/services/current-user/current-user.service';
import { ModalService } from '@shared/services/modal/modal.service';
@Component({
  selector: 'investor-verification-banner',
  templateUrl: './investor-verification-banner.component.html',
  styleUrls: ['./investor-verification-banner.component.scss']
})
export class InvestorVerificationBannerComponent implements OnInit {
  VERIFICATION_STATUS = VERIFICATION_STATUS;
  verificationStatus;
  firstName;
  modalRef: BsModalRef;
  modalConfig = {
    ignoreBackdropClick: true,
    class: 'modal-lg'
  };

  constructor(
    private _dragAndDropService: DragAndDropService,
    private bsModalService: BsModalService,
    private _currentUserService: CurrentUserService,
    private _modalService: ModalService
  ) {
    this._dragAndDropService.updatingVerificationStatus.subscribe(status => {
      this.verificationStatus = status;
    });
  }

  ngOnInit() {
    this.verificationStatus = VERIFICATION_STATUS.APPROVED;
    this._currentUserService.loanUserInfo().then(() => {
      this.firstName = this._currentUserService.getUserInfo().firstName;
      const status = this._currentUserService.getUserInfo().verificationStatus;
      this.verificationStatus = !!status ? status : VERIFICATION_STATUS.NONE;
    });
  }

  showUploadDocumentsModal(e) {
    e.preventDefault();
    this.modalRef = this._modalService.modalInstance(
      InvestorVerificationComponent,
      this.modalConfig
    );
  }
}
