import {
  Component,
  OnInit,
  ChangeDetectorRef,
  AfterViewInit,
  HostListener
} from '@angular/core';
import { PreorderDashboardService } from '@modules/dashboard/api/preorder-dashboard/preorder-dashboard.service';
import { Title } from '@angular/platform-browser';
import { ViewportService } from '@shared/services/viewport/viewport.service';

@Component({
  selector: 'preorder-dashboard',
  templateUrl: './preorder-dashboard.component.html',
  styleUrls: ['./preorder-dashboard.component.scss']
})
export class PreorderDashboardComponent implements OnInit, AfterViewInit {
  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private _preorderDashboardService: PreorderDashboardService,
    private title: Title,
    private _viewportService: ViewportService
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.updateCarouselSize();
      this.isDesktop = this._viewportService.isDesktop();
      this.isTablet = this._viewportService.isTablet();
      this.isMobile = this._viewportService.isMobile();
    });
  }

  isNoPreOrderData = false;
  preOrderDetails = {
    status: 'Not available',
    propertyId: 0,
    propertyName: 'Property Name',
    noOfParts: 0,
    commitmentAmount: 0,
    isEAC: false,
    preferredPaymentMethodId: 1,
    preferredPaymentMethodName: 'ACH Transfer',
    fundingCommitmentDate: '2019-02-15T00:00:00+00:00',
    propertyPhotos: [
      {
        filePath:
          '/Docs/PropertyAsset/Properties/1/PropertyPhotos/Projected3.jpg'
      }
    ]
  };
  slideHeight = `280px`;
  slideWidth = `490px`;
  canAvailPromoDiscount = false;
  promoDiscountMessage = '';
  isDesktop;
  isMobile;
  isTablet;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  ngOnInit() {
    this.title.setTitle(`Partbnb - My Dashboard`);
    this.getPreOrderDetails();
  }

  ngAfterViewInit() {
    this._viewportService.checkViewSizes();
  }

  updateCarouselSize() {
    if (this.preOrderDetails) {
      if (this.isDesktop) {
        this.slideHeight = `280px`;
        this.slideWidth = `490px`;
      } else if (this.isTablet || this.isMobile) {
        const leftWidth = document.querySelector('#pre-order-wrapper-id')
          .clientWidth;
        this.slideHeight = `${leftWidth - 150}px`;
        this.slideWidth = `${leftWidth}px`;
      }
      this.changeDetectorRef.detectChanges();
    }
  }

  getPreOrderDetails() {
    const userName = JSON.parse(localStorage.getItem('currentUser')).username;
    this._preorderDashboardService
      .getPreOrderDetails(userName)
      .subscribe(data => {
        this.preOrderDetails = data;
        this.canAvailPromoDiscount = data.canAvail || false;
        this.promoDiscountMessage = data.promoMessage || '';

        if (
          this.preOrderDetails &&
          this.preOrderDetails.hasOwnProperty('status') &&
          this.preOrderDetails.status === 'Not available'
        ) {
          this.isNoPreOrderData = true;
        } else {
          this.isNoPreOrderData = false;
        }
      });
  }
}
