import { Injectable } from '@angular/core';
import { environment } from '@env/environment';

import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { PreOrderEACDetails } from './preorder-dashboard.model';
import { preorderDashboardBuilderUI } from './preorder-dashboard.mapper';
import { format } from '@shared/utilities/formatEmail';

@Injectable({
  providedIn: 'root'
})
export class PreorderDashboardService {
  baseUrl = environment.apiUrl;

  constructor(private _http: HttpClient) {}

  getPreOrderDetails(userName): Observable<PreOrderEACDetails> {
    return this._http
      .get(`${this.baseUrl}/users/${format(userName)}/temp-dashboard`)
      .pipe(map(preorderDashboardBuilderUI));
  }
}
